.sidebar {
	width: 100vw;
	height: 10vh;
	z-index: 10;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: fixed;
	padding: 1em;
	background-color: $clr-light;

	.navbar {
		display: flex;
		justify-content: center;

		.nav-item {
			padding: 0;
			.nav-icon {
				padding: 0.5em;
				&:hover {
					color: $theme-color;
				}
			}
			&.active {
				.nav-icon {
					color: $theme-color;
				}
			}
			.nav-text {
				display: none;
				text-decoration: none;
				color: $clr-dark;
			}
		}
	}

	.nav-social {
		display: none;
	}

	.copyright {
		display: none;
	}

	@include mq(800px) {
		width: 30vw;
		height: 100vh;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		background: $clr-dark;
		color: $clr-light;
		font-size: 0.9em;
		padding: 3.5em;

		h1 {
			letter-spacing: 0.3em;
			font-size: 1.4em;
		}

		.navbar {
			flex-direction: column;
			margin: 2em 0;
			justify-content: center;

			& > * {
				margin: 0 0.8em;
			}
			.nav-item {
				display: flex;
				align-items: center;
				margin: 0;
				text-align: left;
				& > * {
					margin: 0.2em 0;
					margin-right: 1.2em;
					text-align: left;
					font-size: 0.7em;
					line-height: 1.5em;
					transition: letter-spacing 0.2s linear;
				}
				&.active {
					color: $theme-color !important;
					.nav-text {
						color: $theme-color;
					}
				}

				.nav-icon {
					display: none;
				}

				.nav-text {
					display: block;
					text-decoration: none;
					color: $clr-light;
					text-transform: capitalize;
					letter-spacing: 0.1em;
					&:hover {
						letter-spacing: 0.2em;
						color: $theme-color;
						// transform: scaleX(1.1);
					}
				}
			}
		}
		.nav-social {
			display: flex;
			justify-content: center;
			align-items: center;
			& > * {
				margin: 0.5em;
			}
		}
		.copyright {
			display: block;

			p {
				font-size: 0.9em;
			}
		}
	}
}
