.content {
	display: block;
	width: 100vw;
	height: 100vh;
	background: $clr-dark2;
	position: relative;
	color: $clr-light;
	text-align: center;
	overflow: auto;

	&-home {
		display: flex;
		flex-direction: column;
		align-items: center;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%) scaleX(0);
		width: 100%;
		transform-origin: left;
		padding: 0 1em;
		transition: transform 0.3s ease-in;
		&.active {
			transform: translate(-50%, -50%) scaleX(1);
		}

		.home-image {
			display: block;
			width: 11em;
			height: 11em;
			margin: auto;
			box-decoration-break: clone;
			-webkit-box-reflect: below 10px
				linear-gradient(rgba(0, 0, 0, 0.041), rgba(0, 0, 0, 0.274));

			img {
				border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
				border-radius: 50%;
				border: 0.4em solid rgba(255, 255, 255, 0.534);
				animation: morph 8s ease-in infinite 1s;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.home-typo {
			.home-text {
				margin: 2em 0;
				h1 {
					text-transform: uppercase;
					font-size: 1.3em;
					margin: 0.5em 0;
				}
				p {
					font-size: 0.8em;
					font-style: oblique;
				}
			}

			.home-icons {
				display: flex;
				justify-content: center;
				& > * {
					margin: 0 1em;
					transition: transform 0.3s ease-in-out;
				}
				& > *:hover {
					color: $theme-color;
					transform: translateY(-0.1em) scale(1.1);
				}
			}
		}
	}

	@include mq(800px) {
		&-home {
			padding-left: 32vw;
			// left: 70%;
			// transform: translate(-70%, -50%);
			// background: red;
		}
	}

	@include mq(1000px) {
		padding-left: 30vw;
		text-align: left;
		&-home {
			padding-left: 1em;
			flex-direction: row;
			left: 100%;
			top: 50%;
			width: 70%;
			transform: translate(-100%, -50%) scaleX(0);
			&.active {
				transform: translate(-100%, -50%) scaleX(1);
			}
			.home-image {
				padding: 0;
				margin: 0;
				margin-right: 2em;
				min-width: 11em;
				min-height: 11em;
				-webkit-box-reflect: right 10px
					linear-gradient(rgba(0, 0, 0, 0.041), rgba(0, 0, 0, 0.041));
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			.home-typo {
				display: block;
				// flex-direction: column;
				// width: 100%;
				.home-text {
					h1 {
						font-size: 1.7em;
					}
				}
				.home-icons {
					justify-content: flex-start;
					& > * {
						margin-left: 0;
					}
				}
			}
		}
	}

	&-about {
		position: absolute;
		top: 0%;
		left: 50%;
		transform: translate(-50%, 0%) scaleX(0);
		width: 100%;
		min-height: 100vh;
		display: none;
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 1em;
		padding-top: 6em;
		margin-bottom: 1em;
		transform-origin: left;
		transition: transform 0.3s ease;
		&.active {
			transform: translate(-50%, 0%) scaleX(1);
		}

		@include mq(800px) {
			// padding-top: 0em;
			padding-left: 32vw;
		}

		.about-image {
			width: 100%;
			height: 10em;
			@include mq(600px) {
				width: 100%;
				height: 15em;
			}
			@include mq(800px) {
				width: 100%;
				height: 15em;
			}
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				object-position: top;
				image-orientation: flip;
			}
		}
		.abt {
			display: flex;
			flex-direction: column;
			margin: 2em 0;
			align-items: center;
			.about-text {
				margin-bottom: 1em;
				text-align: left;
				h1 {
					font-size: 1.1em;
					padding: 1em 0;
				}
				p {
					font-size: 0.8em;
				}
			}
			.about-details {
				margin-bottom: 1em;
				display: block;
				width: 100%;
				ul {
					list-style: none;
					text-align: left;
					padding: 0;
					margin: 0;
					font-size: 0.8em;

					li {
						display: grid;
						grid-template-columns: 5em 1fr;
						justify-content: flex-start;
						span {
							display: block;
							// margin-right: 2em;
						}
					}
				}
			}
			@include mq(1200px) {
				justify-content: center;
				align-items: center;
				display: flex;
				flex-direction: row;
				font-size: 0.8em;
				& > * {
					padding-right: 2em;
				}
			}
		}
	}

	&-portfolio {
		position: absolute;
		top: 0%;
		left: 50%;
		transform: translate(-50%, 0%) scaleX(0);
		transform-origin: left;
		width: 100%;
		min-height: 100vh;
		margin-top: 5em;
		margin-bottom: 2em;
		padding: 1em;
		text-align: left;
		transition: transform 0.3s ease;
		&.active {
			transform: translate(-50%, 0%) scaleX(1);
		}
		@include mq(800px) {
			margin-top: 5em;
			padding-left: 35%;
		}
		&-tag {
			display: inline-block;
			padding: 0.5em 1em;
			background: $clr-dark;
			text-transform: uppercase;
			font-size: 0.8em;
		}
		&-filter {
			display: grid;
			grid-template-columns: 1fr;
			margin: 1em 0;
			ul {
				li {
					a {
						&.fil-active {
							color: $theme-color;
						}
					}
				}
			}
			@include mq(1200px) {
				grid-template-columns: 1fr 1fr;
			}
			h3 {
				font-size: 1.7em;
			}
			ul {
				@include mq(800px) {
					font-size: 0.8em;
				}
				@include mq(1200px) {
					font-size: 0.6em;
				}
				list-style: none;
				display: flex;
				flex-wrap: wrap;
				margin: 1em 0;
				padding: 0;
				li {
					margin-right: 1.5em;
					margin-top: 0.5em;
					font-size: 1em;

					a {
						text-decoration: none;
						color: $clr-light;
						transition: letter-spacing 0.2s ease-in, transform 0.2s ease-in;
						&:hover {
							letter-spacing: 0.1em;
							transform: scale(1.1);
						}
					}
				}
			}
		}
		&-projects {
			ul {
				display: grid;
				grid-template-columns: repeat(auto-fit, minmax(13em, max-content));
				list-style: none;
				margin: 0;
				padding: 0;
				grid-gap: 2em;
				position: relative;
				li {
					cursor: pointer;
					overflow: hidden;
					transform: scaleY(0);
					position: absolute;
					width: 0;
					height: 0;
					transition: transform 0.4s ease-in;
					transform-origin: top center;
					&.all.active {
						transform: scaleY(1);
						position: relative;
						width: 100%;
						height: 100%;
					}
					&.active {
						position: relative;
						transform: scaleY(1);
						width: 100%;
						height: 100%;
					}
					.img {
						img {
							width: 100%;
							height: 100%;
							object-fit: cover;
							transition: transform 0.3s linear;
							transform-origin: bottom;
						}
					}
					.details {
						position: absolute;
						display: block;
						text-align: center;
						bottom: 0%;
						left: 0;
						width: 100%;
						height: 50%;
						transform: scaleY(0);
						transform-origin: bottom;
						transition: transform 0.3s linear;
						background: $clr-dark-opac;
						// padding: 1em;
						// margin: 0;
						h1 {
							text-transform: uppercase;
							// margin: 0.5em;
						}
						a {
							margin: 1em;
							color: $clr-light;
						}
					}
					&:hover .details {
						transform: scaleY(1);
					}
					&:hover .img img {
						transform: scaleY(1.1);
					}
				}
			}
		}
	}

	&-skills {
		position: absolute;
		top: 0%;
		left: 50%;
		transform: translate(-50%, 0%) scaleX(0);
		transform-origin: left;
		width: 100%;
		min-height: 100vh;
		margin-top: 5em;
		margin-bottom: 2em;
		padding: 1em;
		text-align: left;
		transition: transform 0.3s ease;
		display: grid;
		grid-gap: 2em;
		grid-template-areas: 'st' 'fe' 'be' 'ts';
		grid-template-rows: min-content;
		text-transform: capitalize;
		&.active {
			transform: translate(-50%, 0%) scaleX(1);
		}
		@include mq(600px) {
			font-size: 0.8em;
			grid-template-areas: 'st .' 'fe be' 'ts ts';
		}
		@include mq(800px) {
			grid-template-areas: 'st' 'fe' 'be' 'ts';
			margin-top: 5em;
			padding-left: 35%;
		}
		@include mq(1200px) {
			grid-template-areas: 'st .' 'fe be' 'ts ts';
		}

		.skill-tag {
			grid-area: st;
			p {
				font-size: 1em;
				letter-spacing: 0.1em;
			}
		}

		ul {
			list-style: none;
			padding: 0;

			li {
				margin: 2em 0;
				display: grid;
				grid-template-areas:
					'name perc'
					'prog prog';
				// grid-template-columns: 1fr 1fr;
				grid-gap: 0.5em;
				.name {
					grid-area: name;
					text-transform: uppercase;
				}
				.percent {
					grid-area: perc;
					text-align: right;
				}
				.progress {
					display: block;
					grid-area: prog;
					color: $clr-dark;
					background-color: $clr-dark;
					width: 100%;
					height: 3px;
					border-radius: 10px;
					span {
						display: block;
						background-color: $theme-color;
						width: 90%;
						height: 3px;
						border-radius: 10px;
						z-index: 100;
					}
				}
			}
		}

		&-FE {
			grid-area: fe;
			height: auto;
			& > * {
				margin: 1em 0;
			}
		}
		&-BE {
			height: auto;
			// @include mq(600px) {
			// 	margin-top: 3.5em;
			// }
			// @include mq(800px) {
			// 	margin-top: 3.5em;
			// }
			// @include mq(800px) {
			// 	margin-top: 0em;
			// }
			// @include mq(1200px) {
			// 	margin-top: 3.5em;
			// }
			& > * {
				margin: 1em 0;
			}
			grid-area: be;
		}
		&-tools {
			grid-area: ts;
			ul {
				display: flex;
				flex-wrap: wrap;
				margin: 1em 0;
				li {
					// padding: 0.2em;
					display: flex;
					align-items: center;
					justify-content: center;
					margin: 1em;
					border: 1px solid $clr-light;
					background: $clr-dark-opac;
					border-radius: 10px;
					flex-basis: 100%;
					// max-width: 8em;
					// min-height: 3em;
					max-width: 6em;
					height: 3em;
					font-size: 0.8em;
					text-transform: uppercase;
				}
			}
		}
	}

	&-contact {
		position: absolute;
		top: 0%;
		left: 50%;
		transform: translate(-50%, 0%) scaleX(0);
		transform-origin: left;
		width: 100%;
		min-height: 100vh;
		margin-top: 5em;
		margin-bottom: 2em;
		padding: 1em;
		text-align: left;
		transition: transform 0.3s ease;
		&.active {
			transform: translate(-50%, 0%) scaleX(1);
		}
		@include mq(800px) {
			padding-left: 32vw;
			font-size: 0.8em;
		}
		& > * {
			margin: 2em 0;
		}
		&-tag {
			p {
				font-size: 1em;
				letter-spacing: 0.1em;
			}
		}
		h1 {
			font-size: 2em;
		}
		&-map {
			#map {
				height: 20em;
				width: 100%;
			}
		}
		#{&}-form {
			display: block;
			& > * {
				width: 100%;
				margin: 1em 0;
				background: none;
				padding: 0.5em 1em;
				border: 1px solid #fff;
			}
			& > *::placeholder {
				color: #fff;
				font-style: italic;
				text-transform: capitalize;
			}

			.message {
				height: 8em;
				&::placeholder {
					display: flex;
					justify-content: center;
					align-items: center;
					line-height: 1em;
				}
			}
			.btn {
				max-width: 20em;
				background: $theme-color;
				// color: $clr-light;
				border: none;
				@include mq(800px) {
					font-size: 0.8em;
				}
			}
		}
	}
}

@keyframes morph {
	0% {
		border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
	}
	50% {
		border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
	}
	100% {
		border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
	}
}
